	max-width: 200px;
	padding: 0 8px;
	vertical-align: top;
	.md-field {
		min-height: initial;
		padding-top: 0;
		margin-top: 0;
	}
	&:last-of-type {
		width: 80px;
		white-space: nowrap;
	}
	.md-button {
		margin: 0;
	}
}

.changed {
	background-color: lightgoldenrodyellow;
}
